    --warning: 38 75% 55%;
    --warning-foreground: 0 0% 100%;

    --info: 199 80% 55%;
    --info-foreground: 0 0% 100%;

    --border: 217 33% 20%;
    --input: 217 33% 20%;
    --ring: 217 91% 60%;

    --sidebar: 222 47% 8%;
    --sidebar-foreground: 210 40% 98%;
    --sidebar-border: 217 33% 17%;
    --sidebar-accent: 217 33% 14%;
