    --glass-bg: rgba(15, 23, 42, 0.7);
    --glass-border: rgba(255, 255, 255, 0.08);
    --glass-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.3);

    /* Gradient Variables for ProgressRing */
    --gradient-start: 217 91% 60%;
    --gradient-end: 263 70% 60%;
  }

